import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  Row,
  Col,
  Divider,
  DatePicker,
  TreeSelect
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/stockValuationSummaryByItemReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import MovementWrapper, {
  Box,
  // StatusTag,
  customCollapseStyle
  // StatusBadge,
  // TransNo
} from "../../../App/app.style";

import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectItem = "/dashboard/setup/items";

class StockValuationSummaryByItemReport extends Component {
  state = {
    filter: {
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null,
      id_item_category: null,
      sort_by: "item_code",
      sort_type: "asc"
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getStockValuationSummaryByItemReports } = this.props;
    getStockValuationSummaryByItemReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.stockValuationSummaryByItemReport.error) {
      notification("error", this.props.stockValuationSummaryByItemReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.stockValuationSummaryByItemReport.success) {
      notification(
        "success",
        this.props.stockValuationSummaryByItemReport.success
      );
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.start_date && filter.end_date) {
      string +=
        "start_date=" + filter.start_date + "&end_date=" + filter.end_date;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.id_item_category) {
      string
        ? (string += "&id_item_category=" + filter.id_item_category)
        : (string += "id_item_category=" + filter.id_item_category);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getStockValuationSummaryByItemReports } = this.props;
    //const { current_page } = this.props.stockValuationSummaryByItemReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getStockValuationSummaryByItemReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getStockValuationSummaryByItemReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null,
        id_item_category: null,
        sort_by: "item_code",
        sort_type: "asc"
      }
    });
    getStockValuationSummaryByItemReports();
  };

  render() {
    let columns = [
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        rowKey: "item_code",
        width: "10%",
        render: (text, row) => (
          <Link to={`${redirectItem}/update/${row.id_item}`}>{text}</Link>
        )
      },
      // {
      //  title: getLanguage({id: "barcode"}),
      //   dataIndex: "barcode",
      //   rowKey: "barcode",
      //   width: "15%",
      //   render: text => <span>{text}</span>
      // },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        rowKey: "item_name",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "opening" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "qty" }),
            dataIndex: "qty_ob",
            rowKey: "qty_ob",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "value" }),
            dataIndex: "total_cost_ob",
            rowKey: "total_cost_ob",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          }
        ]
      },
      {
        title: getLanguage({ id: "totalIn" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "qty" }),
            dataIndex: "qty_in",
            rowKey: "qty_in",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "value" }),
            dataIndex: "total_cost_in",
            rowKey: "total_cost_in",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          }
        ]
      },
      {
        title: getLanguage({ id: "totalOut" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "qty" }),
            dataIndex: "qty_out",
            rowKey: "qty_out",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "value" }),
            dataIndex: "total_cost_out",
            rowKey: "total_cost_out",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          }
        ]
      },
      {
        title: getLanguage({ id: "ending" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "qty" }),
            dataIndex: "qty_balance",
            rowKey: "qty_balance",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "value" }),
            dataIndex: "total_cost_balance",
            rowKey: "total_cost_balance",
            align: "right",
            width: "10%",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          }
        ]
      }
    ];

    // Data setup
    const {
      stock_valuation_summary_by_item_reports,
      locations,
      loading,
      loadingForeign,
      item_categories
    } = this.props.stockValuationSummaryByItemReport
      ? this.props.stockValuationSummaryByItemReport
      : [];
    const {
      filter,
      filter_select
    } = this.props.stockValuationSummaryByItemReport;
    //console.log(filter);

    let dataSource = [...stock_valuation_summary_by_item_reports];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const loop = array =>
      array.map(data => {
        let perData = {
          title: data.item_category_code + " (" + data.item_category_name + ")",
          value: data.id_item_category,
          key: data.id_item_category,
          children: data.children ? loop(data.children) : null
        };

        return perData;
      });

    const fileName = getLanguage({ id: "stockValuationSummaryByItemReports.pageHeader" })

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={
              <IntlMessages id="stockValuationSummaryByItemReports.pageHeader" />
            }
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="stockValuationSummaryByItemReports.pageHeader" />
              }
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.6}
              title="stockValuationSummaryByItemReports.pageHeader"
            />

            <ExcelWrapper
              title="stockValuationSummaryByItemReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="items.itemCategory" />
                    </label>
                    <br />

                    <TreeSelect
                      placeholder={
                        <IntlMessages id="items.select.itemCategory" />
                      }
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_item_category"
                      )}
                      style={{ width: "100%" }}
                      allowClear={true}
                      showSearch={true}
                      treeNodeFilterProp="title"
                      dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                      treeData={loop(item_categories)}
                      treeDefaultExpandAll
                      loading={loading}
                      value={
                        this.state.filter.id_item_category
                          ? this.state.filter.id_item_category
                          : []
                      }
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "item_code"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="item_code">
                            <IntlMessages id="items.code" />
                          </Option>
                          <Option key={2} value="item_name">
                            <IntlMessages id="itemName" />
                          </Option>
                          <Option key={3} value="qty_ob">
                            <IntlMessages id="qtyOb" />
                          </Option>
                          <Option key={4} value="qty_in">
                            <IntlMessages id="qtyIn" />
                          </Option>
                          <Option key={5} value="qty_out">
                            <IntlMessages id="qtyOut" />
                          </Option>
                          <Option key={6} value="qty_balance">
                            <IntlMessages id="qtyBalance" />
                          </Option>
                          <Option key={7} value="total_cost_ob">
                            <IntlMessages id="totalCostOb" />
                          </Option>
                          <Option key={8} value="total_cost_in">
                            <IntlMessages id="totalCostIn" />
                          </Option>
                          <Option key={9} value="total_cost_out">
                            <IntlMessages id="totalCostOut" />
                          </Option>
                          <Option key={10} value="total_cost_balance">
                            <IntlMessages id="totalCostBalance" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                      //disabled={!this.state.filter.id_location}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <MovementWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : dataSource.length === 0 &&
              !this.props.stockValuationSummaryByItemReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="stockValuationSummaryByItemReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    rowKey={record => record.id_item}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </MovementWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    stockValuationSummaryByItemReport: state.stockValuationSummaryByItemReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  StockValuationSummaryByItemReport
);
